.portfolio {
  display: flex;
  width: 100%;
  min-height: 100vh;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 80px;
  box-sizing: border-box;
}

.portfolio-left {
  position: fixed;
  width: 40%;
  max-width: 600px;
  height: 100vh;
  padding: 90px 60px 60px 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.portfolio-right {
  width: 60%;
  margin-left: 45%;
  padding: 90px 0 60px 60px;
  box-sizing: border-box;
  max-width: 700px;
}

.cv-container {
  display: flex;
  min-height: 100vh;
  background-color: var(--bg-color);
  color: var(--text-color);
}

.cv-container .portfolio-left {
  width: 25%;
  max-width: 400px;
  padding-top: 90px;
}

.cv-container .portfolio-right {
  width: 75%;
  margin-left: 28%;
  max-width: 900px;
  padding-top: 90px;
}

.cv-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.cv-nav a {
  display: block;
  padding: 12px 40px;
  color: var(--text-color);
  text-decoration: none;
  opacity: 0.7;
  transition: all 0.2s;
}

.cv-nav a:hover,
.cv-nav a.active {
  opacity: 1;
  background-color: rgba(255, 255, 255, 0.05);
  color: var(--accent-color);
}

.cv-content {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-width: 100%;
  margin-top: 0;
}

.cv-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
}

.cv-header h1 {
  font-size: 36px;
  margin: 0;
}

.pdf-download {
  padding: 8px 16px;
  background-color: rgba(0, 102, 255, 0.1);
  color: var(--accent-color);
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.pdf-download:hover {
  background-color: rgba(0, 102, 255, 0.2);
}

.cv-section {
  margin-bottom: 40px;
  scroll-margin-top: 90px;
}

.cv-section h2 {
  font-size: 16px;
  margin-bottom: 8px;
  color: var(--text-color-bright);
  position: relative;
  z-index: 2;
  background-color: var(--bg-color);
  padding-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

.cv-section h2::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 1px;
  background: rgba(255, 255, 255, 0.15);
}

.info-table,
.cv-content {
  margin-top: 4px;
}

.info-table {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.cv-item {
  margin-bottom: 24px;
}

.info-row {
  display: flex;
  align-items: center;
  min-height: 28px;
}

.info-label {
  width: 120px;
  font-weight: 500;
  color: var(--text-color);
  opacity: 0.8;
}

.info-value {
  flex: 1;
  color: var(--text-color-bright);
}

.info-link {
  color: var(--accent-color);
  text-decoration: none;
  transition: color 0.2s;
}

.info-link:hover {
  opacity: 0.8;
}

.domain-part {
  color: var(--text-color);
  opacity: 0.8;
}

.phone-part {
  color: var(--accent-color);
}

.cv-contact {
  display: flex;
  gap: 24px;
  opacity: 0.8;
  font-size: 15px;
}

.cv-social {
  display: flex;
  gap: 16px;
}

.cv-social a {
  color: inherit;
  text-decoration: none;
  transition: color 0.2s;
}

.cv-social a:hover {
  color: var(--accent-color);
}

.cv-item h3 {
  font-size: 18px;
  margin-bottom: 8px;
  color: var(--text-color-bright);
}

.cv-subtitle {
  font-size: 15px;
  opacity: 0.8;
  margin-bottom: 2px;
}

.cv-date {
  font-size: 14px;
  opacity: 0.7;
  margin-bottom: 8px;
}

.cv-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 3px;
  margin-top: 16px;
  list-style: none;
  padding: 0;
}

.cv-skills li {
  font-size: 13px;
  padding: 2px 8px;
  background: rgba(0, 102, 255, 0.05);
  color: var(--accent-color);
  border-radius: 4px;
}

.skills-grid {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 2px;
}

.skill-category {
  width: 100%;
}

.skill-category h4 {
  font-size: 16px;
  margin: 2px 0px;
  color: var(--text-color-bright);
}

.skill-category ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.skill-category li {
  font-size: 14px;
  margin-bottom: 4px;
  opacity: 0.8;
}

@media (max-width: 1024px) {
  .cv-container .portfolio-left {
    width: 100%;
    position: relative;
    padding: 40px 0;
  }

  .cv-container .portfolio-right {
    width: 100%;
    margin-left: 0;
    padding: 40px 0;
  }
}

.cv-container .portfolio-left h1,
.cv-container .header-content h1 {
  font-size: 24px; 
  line-height: 1.2;
}

.cv-container .portfolio-left h2,
.cv-container .header-content h2 {
  font-size: 13px;
  margin: 4px 0 8px 0;
  opacity: 0.8;
}

.info-grid {
  display: none;
}

.email-separator {
  color: var(--text-color);
  opacity: 0.7;
  margin: 0 8px;
}

.cv-container .section-nav .nav-link {
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.5px;
  opacity: 0.7;
  transition: all 0.2s;
}

.cv-container .section-nav .nav-link:hover {
  color: var(--accent-color);
  opacity: 1;
}

.cv-container .section-nav ul {
  gap: 4px;
}

.cv-container .section-nav .nav-link {
  padding: 6px 0;
}

.education-item {
  margin-top: 8px;
}

.education-header {
  display: flex;
  gap: 12px;
  align-items: flex-start;
}

.education-date {
  font-size: 13px;
  color: var(--text-color);
  opacity: 0.7;
  background: rgba(0, 102, 255, 0.05);
  padding: 4px 8px;
  border-radius: 4px;
  white-space: nowrap;
}

.education-main {
  flex: 1;
}

.education-main h3 {
  font-size: 16px;
  color: var(--text-color-bright);
  margin-bottom: 8px;
}

.education-school {
  font-size: 14px;
  color: var(--text-color);
  opacity: 0.7;
  margin-bottom: 12px;
}

.education-detail {
  font-size: 14px;
  color: var(--text-color);
  opacity: 0.7;
  margin-bottom: 4px;
}

.course-pills {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 4px;
}

.course-pill {
  font-size: 13px;
  padding: 2px 8px;
  background: rgba(0, 102, 255, 0.05);
  color: var(--accent-color);
  border-radius: 4px;
  white-space: nowrap;
}


.experience-item .company {
  margin-top: -4px; 
  margin-bottom: 4px; 
}


.experience-item h4 {
  margin-bottom: 4px; 
}


.cv-container .name-link {
  color: inherit;
  text-decoration: none;
  transition: color 0.2s;
}

.cv-container .name-link:hover {
  color: var(--accent-color);
}

.projects-grid {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.project-card {
  padding: 20px;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.project-card:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.project-card {
  background-color: transparent;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.project-title {
  margin: 0;
  font-size: 20px;
  color: var(--text-color-bright);
}

.project-title a {
  color: var(--text-color-bright);
  text-decoration: none;
}

.project-title a:hover {
  color: var(--accent-color);
}

.project-links {
  display: flex;
  gap: 10px;
}

.external-link {
  color: var(--text-color);
  font-size: 20px;
  transition: color 0.2s;
}

.external-link:hover {
  color: var(--accent-color);
}

.project-description {
  color: var(--text-color);
  font-size: 14px;
  margin-bottom: 15px;
  opacity: 0.8;
}

.project-tech-list {
  display: flex;
  flex-wrap: wrap;
  gap: 3px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.project-tech-list li {
  font-size: 13px;
  padding: 2px 8px;
  background: rgba(0, 102, 255, 0.05);
  color: var(--accent-color);
  border-radius: 4px;
  white-space: nowrap;
}


.cv-section h2 {
  position: relative;
  z-index: 2; 
  background-color: var(--bg-color); 
  padding-bottom: 8px; 
}

.experience-item {
  margin: 4px -32px 8px -32px; 
  padding: 16px 32px;
  border-radius: 12px;
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
}


.cv-section#education .experience-item:first-child {
  margin-top: 4px; 
}

.experience-item .role-details h4 {
  margin-bottom: 8px;
}

.experience-item .company {
  margin-bottom: 12px;
}

.experience-item .description {
  margin: 12px 0;
} 

.cv-section#education .experience-item .cv-skills {
  margin-bottom: 8px; 
}


.project-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px;
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
}
