:root {
  --bg-color: white;
  --text-color: #333;
  --text-color-bright: #000;
  --accent-color: #0066ff;
  --font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  font-family: var(--font-family);
  line-height: 1.6;
  scroll-behavior: smooth;
  margin: 0;
}

.portfolio {
  display: flex;
  width: 100%;
  min-height: 100vh;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 150px;
  box-sizing: border-box;
}

.portfolio-left {
  position: fixed;
  width: 40%;
  max-width: 480px;
  height: 100vh;
  padding: 100px 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.portfolio-right {
  width: 60%;
  margin-left: 40%;
  padding: 100px 0 60px 100px;
  box-sizing: border-box;
  max-width: 800px;
}

header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.top-content {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.header-content {
  margin-bottom: 0;
}

h1 {
  font-size: 56px;
  font-weight: 600;
  margin: 0;
  color: var(--text-color-bright);
  line-height: 1.1;
  letter-spacing: -0.02em;
}

h2 {
  font-size: 16px;
  font-weight: 500;
  color: var(--text-color);
  margin: 10px 0 20px 0;
  opacity: 0.9;
}

.tagline {
  color: var(--text-color);
  font-size: 16px;
  max-width: 540px;
  margin: 20px 0 0 0;
  line-height: 1.5;
}

.section-nav {
  margin: 0;
}

.section-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.nav-link {
  background: none;
  border: none;
  color: var(--text-color);
  font-size: 14px;
  padding: 0;
  cursor: pointer;
  transition: color 0.2s;
  text-decoration: none;
  text-align: left;
  font-weight: 500;
  letter-spacing: 0.5px;
}

.nav-link:hover {
  color: var(--accent-color);
}

.nav-link.active {
  color: var(--accent-color);
}

.experience-item, .project-item {
  display: flex;
  gap: 40px;
  padding: 32px;
  margin: -32px -32px 32px -32px;
  border-radius: 12px;
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
  position: relative;
}

.experience-item:first-child,
.project-item:first-child {
  margin-top: -32px;
}

.experience-item:last-child,
.project-item:last-child {
  margin-bottom: -32px;
}

.experience-item:hover,
.project-item:hover {
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(4px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
}

.experience-item::after, .project-item::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 12px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
  opacity: 0;
}

.experience-item:hover::after, .project-item:hover::after {
  opacity: 1;
}

.experience-item h4,
.project-item h4 {
  transition: color 0.2s ease;
}

.experience-item:hover h4,
.project-item:hover h4 {
  color: var(--accent-color);
}

.date {
  min-width: 140px;
  color: var(--text-color);
  font-size: 13px;
  opacity: 0.6;
  line-height: 1.2;
  padding-top: 4px;
}

.role-details h4 {
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 4px 0;
  color: var(--text-color-bright);
  line-height: 1.2;
}

.role-details .secondary-role {
  font-size: 16px;
  color: var(--text-color);
  opacity: 0.7;
  margin: 4px 0;
}

.location {
  color: var(--text-color);
  font-size: 13px;
  margin: 4px 0 12px 0;
  opacity: 0.7;
}

.role-details p {
  font-size: 13px;
  line-height: 1.4;
  margin: 16px 0;
  color: var(--text-color);
  opacity: 0.8;
  text-align: justify;
}

.role-details .description {
  font-size: 13px;
  line-height: 1.4;
  margin: 16px 0;
  color: var(--text-color);
  opacity: 0.8;
  text-align: justify;
}

.skills {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.skill-tag {
  background-color: rgba(0, 102, 255, 0.05);
  color: var(--accent-color);
  padding: 6px 14px;
  border-radius: 4px;
  font-size: 12px;
  letter-spacing: 0.5px;
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
}

.experience-item:hover .skill-tag, .project-item:hover .skill-tag {
  background-color: rgba(0, 102, 255, 0.1);
}

.reference-link {
  color: var(--text-color);
  margin-right: 16px;
  font-size: 14px;
}

@media screen and (max-width: 1024px) {
  .portfolio {
    flex-direction: column;
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin: 0;
    display: block;
  }

  .portfolio-left {
    position: static !important;
    width: 100% !important;
    height: auto !important;
    max-width: none !important;
    padding: 50px 25px 30px !important;
    margin: 0 !important;
  }

  .portfolio-right {
    width: 100% !important;
    margin: 0 !important;
    padding: 0 25px 50px !important;
    max-width: none !important;
  }

  .projects-grid {
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .project-card {
    max-width: 100%;
    margin: 0;
    padding: 16px 4px;
  }

  .experience-item, 
  .project-item {
    width: 100%;
    padding: 25px 0;
    margin: 0;
    gap: 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }

  .experience-item:last-child,
  .project-item:last-child {
    border-bottom: none;
  }

  section {
    margin: 0;
    padding: 0;
  }

  .about p {
    margin: 0 0 16px 0;
  }

  .section-nav {
    display: none !important;
  }

  h1 {
    font-size: 40px;
    margin-bottom: 10px;
  }

  h2 {
    font-size: 16px;
    margin: 5px 0;
  }

  .tagline {
    margin: 20px 0;
    font-size: 15px;
  }

  .role-details h4 {
    font-size: 18px;
    margin-bottom: 5px;
  }

  .role-details .secondary-role {
    font-size: 15px;
    margin: 5px 0;
  }

  .location {
    font-size: 13px;
    margin: 5px 0;
  }

  .role-details p,
  .role-details .description {
    font-size: 14px;
    margin: 15px 0;
    line-height: 1.6;
    opacity: 0.8;
  }

  .date {
    font-size: 13px;
    margin-bottom: 5px;
  }
}

@media screen and (max-width: 768px) {
  .portfolio-left,
  .portfolio-right {
    padding: 35px 20px !important;
  }

  h1 {
    font-size: 32px;
  }
}

@media screen and (max-width: 480px) {
  .portfolio-left,
  .portfolio-right {
    padding: 25px 15px !important;
  }

  h1 {
    font-size: 28px;
  }

  .experience-item, 
  .project-item {
    padding: 20px 0;
  }

  .role-details p,
  .role-details .description,
  .about p {
    padding: 0 2px;
  }

  section {
    padding: 16px 0;
  }
}

section {
  margin-bottom: 120px;
  position: relative;
}

.cv-container section {
  scroll-margin-top: 90px;
}

.portfolio:not(.cv-container) section.about {
  scroll-margin-top: 120px;
}

.portfolio:not(.cv-container) section.work-experience {
  scroll-margin-top: 80px;  
}

.portfolio:not(.cv-container) section.projects {
  scroll-margin-top: 80px;  
}

.about {
  padding-top: 8px;
}

.about p {
  margin: 0 0 16px 0;
  text-align: justify;
}

.about p:last-child {
  margin-bottom: 0;
}

.work-experience {
  padding-top: 0;  
}

.bottom-content {
  margin-top: auto;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.education-brief {
  font-size: 16px;
  color: var(--text-color);
}

.education-brief p {
  margin: 0;
  font-weight: 500;
}

.education-brief .school {
  color: var(--text-color);
  opacity: 0.8;
  font-size: 14px;
  margin-top: 4px;
}

.social-links {
  display: flex;
  gap: 12px;
  align-items: center;
}

.social-links a {
  color: var(--text-color);
  font-size: 32px;
  transition: color 0.2s;
}

.social-links a:hover {
  color: var(--accent-color);
}

.social-links .icon-button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  color: var(--text-color);
  font-size: 32px;
  cursor: pointer;
  transition: color 0.2s;
  display: flex;
  align-items: center;
  height: 32px;
}

.social-links .icon-button:hover {
  color: var(--accent-color);
}

section:first-of-type p:first-child {
  margin-top: 0;
}

.projects {
  padding-top: 0;  
}

.cv-content {
  margin-top: 8px;
}

.projects-grid {
  margin: -32px -32px 32px -32px;
  padding-left: 12px;
  margin-top: 8px;
}

.project-card {
  display: flex;
  align-items: flex-start;
  gap: 15px;
  padding: 20px;
  padding-left: 0;
  margin-bottom: 0;
  position: relative;
  max-width: calc(100% - 32px);
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
}

.project-card::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 12px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
  opacity: 0;
}

.project-card:hover::after {
  opacity: 1;
}

.project-card:hover {
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(4px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
}

.project-card:hover .project-title {
  color: var(--accent-color);
}

.view-resume {
  display: inline-block;
  color: var(--text-color);
  text-decoration: none;
  font-size: 16px;
  margin-top: 0px;
  padding: 0px 32px;
  margin-left: -32px;
  opacity: 1;
  transition: color 0.2s;
}

.view-resume:hover {
  color: var(--accent-color);
  opacity: 1;
}

.view-resume::after {
  display: none;
}

.company-link {
  color: inherit;
  text-decoration: none;
  transition: color 0.2s;
  position: relative;
  z-index: 1;
  cursor: pointer;
}

.company-link:hover {
  color: var(--accent-color);
}

.experience-item:hover .company-link {
  pointer-events: auto;
}

.footer {
  margin-top: 40px;
  padding-top: 20px;
}

.footer p {
  color: var(--text-color);
  font-size: 13px;
  line-height: 1.5;
  opacity: 0.7;
}

.footer a {
  color: var(--text-color);
  text-decoration: none;
  transition: color 0.2s;
  padding: 0 2px;
}

.footer a:hover {
  color: var(--accent-color);
}

.name-link {
  color: var(--text-color-bright);
  text-decoration: none;
  transition: color 0.2s;
}

.name-link:hover {
  color: var(--accent-color);
}

.contact-info {
  margin-top: 16px;
  font-size: 14px;
  color: var(--text-color);
  opacity: 0.8;
}

.contact-link {
  color: inherit;
  text-decoration: none;
  transition: color 0.2s;
}

.contact-link:hover {
  color: var(--accent-color);
}

.contact-divider {
  margin: 0 8px;
}

.contact-location {
  color: inherit;
}

.contact-social {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.subtitle {
  margin: 8px 0 16px;
  font-size: 14px;
  color: var(--text-color);
  opacity: 0.8;
}

.subtitle .contact-link {
  color: inherit;
  text-decoration: none;
  transition: color 0.2s;
}

.subtitle .contact-link:hover {
  color: var(--accent-color);
}

.subtitle .contact-divider {
  margin: 0 8px;
}

.middle-content {
  margin: auto 0;
  padding: 40px 0;
}

.contact-links {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.contact-links .contact-link {
  color: var(--text-color);
  text-decoration: none;
  font-size: 14px;
  transition: color 0.2s;
  opacity: 0.7;
}

.contact-links .contact-link:hover,
.social-links a:hover {
  color: var(--accent-color);
  opacity: 1;
}

h1 a {
  color: inherit;
  text-decoration: none;
}

h1 a:hover {
  color: var(--accent-color);
}

.project-image {
  flex: 0 0 135px;
  border-radius: 4px;
  overflow: hidden;
}

.image-wrapper {
  position: relative;
  padding-top: 56.25%;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  overflow: hidden;
}

.project-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px;
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
}

.project-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding-top: 0px;
}

.project-title {
  font-size: 20px;
  margin: 0;
  color: var(--text-color-bright);
  line-height: 1;
}

.project-description {
  font-size: 14px;
  margin: 8px 0;
  line-height: 1.4;
  background-color: rgba(255, 255, 255, 0.05);
  color: var(--text-color);
  padding: 0px;
  border-radius: 4px;
  text-align: justify;
}

.project-tech-list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  padding: 0;
  margin: 8px 0 0;
  list-style: none;
}

.project-tech-list li {
  font-size: 13px;
  color: var(--accent-color);
  white-space: nowrap;
}

.project-card .project-header {
  margin: 0;
}

.projects-grid .project-card .project-header {
  margin: 0 !important;
}

.tooltip-container {
  position: relative;
  display: flex;
  align-items: center;
}

.tooltip {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--text-color);
  color: var(--bg-color);
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  white-space: nowrap;
  pointer-events: none;
  animation: fadeInOut 2s ease;
  z-index: 10;
}

@keyframes fadeInOut {
  0% { opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { opacity: 0; }
}

.tech-pill {
  font-size: 12px;
  color: var(--accent-color);
  white-space: nowrap;
  background-color: rgba(0, 102, 255, 0.05);
  padding: 4px 10px;
  border-radius: 4px;
  letter-spacing: 0.5px;
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
}

.experience-item:hover .tech-pill,
.project-card:hover .tech-pill {
  background-color: rgba(0, 102, 255, 0.1);
}

.tech-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 0;
  margin: 8px 0 0;
  list-style: none;
}

.skills, .project-tech-list {
  display: none;
}

.project-stats {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-right: 12px;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 4px;
  color: var(--text-color);
  font-size: 14px;
  opacity: 0.7;
}

.stat-item svg {
  font-size: 16px;
}

.project-links {
  display: flex;
  align-items: center;
}

.project-links .external-link {
  color: var(--text-color);
  opacity: 0.7;
  transition: color 0.2s;
  font-size: 16px;
}

.project-links .external-link:hover {
  color: var(--accent-color);
  opacity: 1;
}

.cv-section h2 {
  margin: 0;
  padding: 0;
  line-height: 1;
}

.experience-item:first-of-type {
  margin-top: 0;
}

.cv-section > div:first-of-type {
  margin-top: 0;
}

